<template>
  <div id="app">
      <div class="buttons">
          <input type="button" @click="getMa" value="TEST BUTTON" style="background-color: red; color: white">
          <select class="button" v-model="selectedType">
                  <option value="graffity">ГРАФФИТИ</option>
                  <option value="streetart">СТРИТ-АРТ</option>
          </select>
          <select class="button" v-model="selectedStatus">
                  <option value="onspot">НА УЛИЦЕ</option>
                  <option value="buff">БАФФ</option>
          </select>
          <select class="button" v-model="selectedAuthor">
                  <option value="all">ВСЕ ХУДОЖНИКИ</option>
                  <option v-for="(author, index) in uniqueAuthors" :key="index" :value="author">{{ author }}</option>
          </select>
          <!-- <input type="button" class="button" value="Добавить метку"> -->
          <!-- <label for="">now filters: {{ filters }}</label> -->
          <input class="button" type="button" value="ИНФО">
      </div>
      <div class="union">
          <div class="cards">
              <loader v-if="loading"></loader>
              <marks-list v-else-if="filteredMarks.length" :filteredMarks="filteredMarks" @locate-on="getCoord"></marks-list>
              <p class="condition" v-else>ТУТ ДОЛЖНЫ БЫЛИ БЫТЬ ФОТКИ :(</p>
          </div>
          <div class="map">
              <loader v-if="loading"></loader>
              <yandex-maps v-else-if="filteredMarks.length" :filteredMarks="filteredMarks" :selectedMarkCoords="selectedMarkCoords"></yandex-maps>
              <p class="condition" v-else>ТУТ ДОЛЖНА БЫЛА БЫТЬ КАРТА :(</p>
          </div>
      </div>
  </div>
</template>

<script>

import Maps from './components/Maps'
import MarkList from './components/MarkList'
import CreateMark from './components/CreateMark'
import Loader from './components/Loader'

export default {
  name: 'App',
  components: {
    'yandex-maps': Maps,
    'marks-list': MarkList,
    'create-mark': CreateMark,
    'loader': Loader
  },
  data () {
    return {
      selectedType: 'streetart',
      selectedStatus: 'onspot',
      selectedAuthor: 'all',
      selectedMarkCoords: [59.93877935, 30.30912970],
      loading: true
    }
  },
  beforeMount () {
    // Перед тем как загрузить страницу, нам нужно получить список всех
    // имеющихся меток. Для этого мы вызываем действие `getMarks` из
    // нашего хранилища
    setTimeout (()=>{
      this.$store.dispatch('getMarks')
      this.loading = false
    }, 500)
  },
  computed: {
    // filters: function () {
    //   return this.selectedType + '+' + this.selectedStatus + '+' + this.selectedAuthor
    // },
    filteredMarks: function () {
      return this.initMarks.filter(this.filterMark)
    },
    initMarks: function () {
      return this.$store.state.marks
    },
    uniqueAuthors: function () {
      var uniqueMarks = this.initMarks.filter(this.filterByTypeStatus)
      var authors = []
      for (let i = 0; i < uniqueMarks.length; i++) {
        if (authors.indexOf(uniqueMarks[i].author) === -1) {
          authors.push(uniqueMarks[i].author.toUpperCase())
        }
      }
      return authors
    }
  },
  methods: {
    getMa () {
      console.log(this.filteredByTypeStatus)
    },
    filterMark (item) {
      if (this.selectedAuthor === 'all') {
        return (item.typeof.match(this.selectedType) && item.status.match(this.selectedStatus))
      } else {
        return (item.author.toUpperCase().match(this.selectedAuthor) && // аппекейс из-за плохого шрифта
               item.typeof.match(this.selectedType) &&
               item.status.match(this.selectedStatus))
      }
    },
    filterByTypeStatus (item) {
      return (item.status.match(this.selectedStatus) && item.typeof.match(this.selectedType))
    },
    getCoord ([latti, longi]) {
      this.selectedMarkCoords = [latti, longi]
    }
  }
}
</script>

