import Vue from 'vue'
import Vuex from 'vuex'
import { Mark } from '../api/marks'
import { SET_MARKS, ADD_MARK } from './mutation-types.js'

Vue.use(Vuex)

const state = {
  marks: []
}

const getters = {
  marks: state => state.marks
}

const mutations = {
  [SET_MARKS] (state, {marks}) {
    state.marks = marks
  },
  [ADD_MARK] (state, mark) {
    state.marks = [mark, ...state.marks]
  }
}

const actions = {
  getMarks ({ commit }) {
    Mark.list().then(marks => {
      commit(SET_MARKS, { marks })
    })
  },
  createMark ({ commit }, markData) {
    Mark.create(markData).then(mark => {
      commit(ADD_MARK, mark)
    })
  }
}

export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations
})
