<template >
    <div class="mymap">
      <yandex-map id="map"
          :settings="settings"
          :coords="coords"
          zoom="12"
          :controls="['zoomControl', 'geolocationControl']"
          :behaviors="['drag', 'scrollZoom', 'dblClickZoom']"
          :initWithoutMarkers="without"
          >
          <div v-for="mark in filteredMarks" :key="mark.id">
              <ymap-marker
                  markerId="mark.id"
                  marker-type="Placemark"
                  :coords="[mark.latti, mark.longi]"
                  :icon="customIcon"
                  :balloon-template="balloonTemplate(mark)"
              >
              </ymap-marker>
          </div>
      </yandex-map>
    </div>
</template>

<script>

import { yandexMap, ymapMarker } from 'vue-yandex-maps'

export default {
  name: 'yandex-maps',
  props: {
    filteredMarks: {
      type: Array,
      // required: true
    },
    selectedMarkCoords: {
      type: Array
    }
  },
  methods: {
    balloonTemplate (mark) {
      return `
      <h3>${mark.name}</h3>
      <img class="balloonImage" src=${mark.photo}>
      <p>Author: ${mark.author}</p>
      <p>${mark.addrdate}</p>
      `
    }
  },
  components: { yandexMap, ymapMarker },
  computed: {
    coords: function () {
      return this.selectedMarkCoords
    },
    
  },
  data () {
    return {
      settings: {
        apiKey: '330241da-79b7-4aa0-bb0a-ff03f342c936',
        lang: 'ru_RU',
        coordorder: 'latlong',
        version: '2.1.76',
        mapType: 'map'},
      without: true,
      // coords: this.newCoords,
      customIcon: {
        layout: 'default#imageWithContent',
        imageHref: 'myIcon.gif',
        imageSize: [35, 45],
        imageOffset: [0, 0],
      }
    }
  }
}
</script>
<style>

.mymap {
  width: 100%;
  height: 100%;
}

#map {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
img.balloonImage {
  width: 70%;
  justify-content: center;
}
[class*="ymaps-2"][class*="-ground-pane"] {
  filter: url("data:image/svg+xml;utf8,<svg xmlns=\'http://www.w3.org/2000/svg\'><filter id=\'grayscale\'><feColorMatrix type=\'matrix\' values=\'0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0\'/></filter></svg>#grayscale");
  /* Firefox 3.5+ */
  -webkit-filter: grayscale(100%);
  /* Chrome 19+ & Safari 6+ */
}
</style>
