<template>
    <form class="form-horizontal" action="/api/" method="POST" @submit="submitForm">
        <div class="form-group">
            <label for="name" class="col-sm-2 control-label">NAME</label>
                <div class="col-sm-10">
                    <input class="form-control" type="text" v-model="name" id="name">
                </div>
        </div>
        <div class="form-group">
            <label for="latti" class="col-sm-2 control-label">LATTI</label>
                <div class="col-sm-10">
                    <input class="form-control" type="text" v-model="latti" id="latti">
                </div>
        </div>
        <div class="form-group">
            <label for="longi" class="col-sm-2 control-label">LONGI</label>
            <div class="col-sm-10">
                <input type="text" class="form-control" v-model="longi" id="longi">
            </div>
        </div>
        <div class="form-group">
            <label class="col-sm-2 control-label" for="author">AUTHOR</label>
            <div class="col-sm-10">
                <input class="form-control" type="text" v-model="author" id="author">
            </div>
        </div>
        <div class="form-group">
            <label class="col-sm-2 control-label" for="addrdate">ADDRDATE</label>
            <div class="col-sm-10">
                <input class="form-control" type="text" v-model="addrdate" id="addrdate">
            </div>
        </div>
        <div class="form-group">
            <label class="col-sm-2 control-label" for="photo">URL photo</label>
            <div class="col-sm-10">
                <input class="form-control" type="text" v-model="photo" id="photo">
            </div>
        </div>
        <div class="form-group">
            <label class="col-sm-2 control-label" for="typeof">TYPE OF WORK</label>
            <div class="col-sm-10">
                <input class="form-control" type="text" v-model="type" id="typeof">
            </div>
        </div>
        <div class="form-group">
            <label class="col-sm-2 control-label" for="status">STATUS</label>
            <div class="col-sm-10">
                <input class="form-control" type="text" v-model="status" id="status">
            </div>
        </div>
        <div class="form-group">
            <div class="col-sm-offset-2 col-sm-10">
                <button type="submit" class="btn btn-default">Добавить метку</button>
            </div>
        </div>
    </form>
</template>

<script>
export default {
  name: 'create-mark',
  data () {
    return {
      'name': '',
      'latti': '',
      'longi': '',
      'author': '',
      'addrdate': '',
      'photo': '',
      'type': '',
      'status': ''
    }
  },
  methods: {
    submitForm (event) {
      this.createMark()
      this.name = ''
      this.latti = ''
      this.longi = ''
      this.author = ''
      this.addrdate = ''
      this.photo = ''
      this.type = ''
      this.status = ''
      event.preventDefault()
    },
    createMark () {
      this.$store.dispatch('createMark', {
        name: this.name,
        latti: this.latti,
        longi: this.longi,
        author: this.author,
        addrdate: this.addrdate,
        photo: this.photo,
        typeof: this.type,
        status: this.status
      })
    }
  }
}
</script>

<style>
.form-horizontal {
    display: block;
    margin: 10px;
    padding: 10px;
    width: 500px;
    background-color: cadetblue;
}
</style>
